import styled from "styled-components";
import { Link } from "gatsby";
import breakpoints from "../layout/breakpoints";

const BaseLink = styled(Link)`
    position: relative;
    font-size: 1rem;
    text-decoration: inherit;
    color: inherit;
    box-sizing: border-box;
    transition-delay: 0.1s;
    border: 0;
    outline: 0;
`;

const TextLink = styled(BaseLink)`
    position: relative;
    display: inline-block;
    font-size: var(--font-size-m);
    font-weight: var(--font-weight-semibold);
    z-index: 0;

    &::after {
        content: "";
        display: block;
        position: absolute;
        bottom: -0.1rem;
        left: 0;
        width: 100%;
        height: 0.1rem;
        background-color: var(--color-primary-1);
        transition: all var(--transition-fast);
        transition-delay: 0.1s;
        pointer-events: none;
        z-index: -1;
    }

    &:hover::after, &:focus::after, &:active::after {
        height: 0.2rem;
    }
`;

const NavLink = styled(BaseLink)`
    position: relative;
    display: inline-block;
    margin: var(--spacing-s) var(--spacing-base);
    font-weight: var(--font-weight-bold);
    font-size: var(--font-size-l);
    text-transform: uppercase;
    z-index: 0;

    &::after {
        content: "";
        display: block;
        position: absolute;
        bottom: 0;
        left: -0.3rem;
        width: 0;
        height: 0.5rem;
        background-color: var(--color-primary-1);
        transition: all var(--transition-fast);
        transition-delay: 0.1s;
        pointer-events: none;
        z-index: -1;
    }

    &:hover::after, &:focus::after, &:active::after {
        width: calc(100% + 0.6rem);
    }

    &[activelink="true"]::after {
            width: calc(100% + 0.6rem);
    }

    .font-loading & {
        font-family: Calibri;
        font-size: 1.25rem;
      }

    ${breakpoints.sm`
        & {
            font-size: var(--font-size-m);
        }
    `}
`;

const ButtonLink = styled(BaseLink)`
    position: relative;
    display: inline-block;
    margin-top: var(--spacing-s);
    padding: var(--spacing-base) var(--spacing-m);
    color: var(--color-neutral-0);
    font-weight: var(--font-weight-bold);
    text-align: center;
    background-color: var(--color-secondary-1);
    transition: all var(--transition-base);

    &:hover, &:focus, &:active {
        padding-left: calc(var(--spacing-m) + var(--spacing-s));
        background-color: var(--color-neutral-9);
        text-decoration: underline;
    }
`;

const AnchorLink = styled.a`
    position: relative;
    display: inline-block;
    font-size: var(--font-size-m);
    font-weight: var(--font-weight-semibold);
    text-decoration: inherit;
    color: inherit;
    box-sizing: border-box;
    transition-delay: 0.1s;
    border: 0;
    outline: 0;
    z-index: 0;

    &::after {
        content: "";
        display: block;
        position: absolute;
        bottom: -0.1rem;
        left: 0;
        width: 100%;
        height: 0.1rem;
        background-color: var(--color-primary-1);
        transition: all var(--transition-fast);
        transition-delay: 0.1s;
        pointer-events: none;
        z-index: -1;
    }

    &:hover::after, &:focus::after, &:active::after {
        height: 0.2rem;
    }
`;

export { BaseLink, TextLink, NavLink, ButtonLink, AnchorLink };