import React from "react";
import styled from "styled-components";
import { BaseLink } from "./links";
import { Logo } from "./navbar";
import breakpoints from "../layout/breakpoints";

//footer logo re-use?

const FooterWrapper = styled.footer`
    position: relative;
    display: flex;
    flex-shrink: 0;
    margin: 0;
    padding: var(--spacing-m) var(--spacing-m) var(--spacing-xl) var(--spacing-m);
    justify-content: center;
    align-items: center;
    background-color: var(--color-neutral-7);
    z-index: 10;
    overflow: hidden;

    ${breakpoints.md`
      & {
        padding: var(--spacing-m) var(--spacing-xl);
      }
    `}
`;

const FooterLogo = styled(Logo)`
    display: none;
    position: absolute;
    z-index: -1;
    top: -3.125rem;
    left: var(--spacing-m);
    color: var(--color-neutral-0);

    ${breakpoints.sm`
      display: block;
    `}
`;


const FooterLinks = styled.div`
    display: inline-block;
`;

const FooterPrimaryLink = styled(BaseLink)`
    color: var(--color-neutral-0);
    margin: 0 var(--spacing-m);
    font-weight: var(--font-weight-bold);

    .dark-mode & {
      color: var(--color-text);
    }
`;

const Footer = () => {
    return (
        <>
        <FooterWrapper>
          <FooterLogo to="/">j</FooterLogo>
            <FooterLinks>
                <FooterPrimaryLink to="/work">Work</FooterPrimaryLink>
                <FooterPrimaryLink to="/contact">Contact</FooterPrimaryLink>
            </FooterLinks>
        </FooterWrapper>
        </>
    )
}

export default Footer;